.overlay {
	position: fixed;
	z-index: 1000;
	background: #000000;
	top: 0;
	left: 0;
	width: 100%;
	height: 0%;
	opacity: 0;
	visibility: hidden;
	transition: opacity .35s, visibility .35s, height .35s;
	overflow: hidden;
}

.overlay.open {
	opacity: .9;
	visibility: visible;
	height: 100%;
}

.overlay nav {
	position: relative;
	height: 70%;
	top: 50%;
	transform: translateY(-50%);
	font-weight: 400;
	text-align: center;
}

.overlay ul {
	list-style: none;
	padding: 0;
	margin: 0 auto;
	display: inline-block;
	position: relative;
	height: 100%;
}

.overlay ul li {
	display: block;
	position: relative;
}

.overlay ul li a {
	display: block;
	position: relative;
	color: #fff;


	text-transform: uppercase;
	font-size: 1.625rem;
	padding: 20px 0;
	text-decoration: none;
	overflow: hidden;
	transition: .35s;
}

.overlay ul li a:after {
	content: '';
	position: absolute;
	bottom: 0;
	left: 50%;
	width: 0%;
	transform: translateX(-50%);
	height: 3px;
	background: #FFF;
	transition: .35s;
}

