@-webkit-keyframes wave {
	0% {
		transform: rotate(0) scale(1);
	}

	33% {
		transform: rotate(-6deg) scale(0.9);
	}

	66% {
		transform: rotate(6deg) scale(0.9);
	}

	100% {
		transform: rotate(0) scale(1);
	}
}

@keyframes wave {
	0% {
		transform: rotate(0) scale(1);
	}

	33% {
		transform: rotate(-6deg) scale(0.9);
	}

	66% {
		transform: rotate(6deg) scale(0.9);
	}

	100% {
		transform: rotate(0) scale(1);
	}
}

