	font-weight: normal;
	margin-bottom: 70px;
}

.price-block-list ul {
	list-style-type: none;
	padding-left: 10px;
}

.price-block-list ul li {
	background: transparent url(../img/icon/list-check-one.png) no-repeat scroll left 2px;
	float: left;
	font-size: 18px;
	line-height: 1.1;
	margin-bottom: 3px;
	min-height: 15px;
	padding-left: 20px;
}

.price-block-list ul li.active {
	background: transparent url(../img/icon/list-check-noy.png) no-repeat scroll left 2px;
}

.price-block-list ul li:last-child {
	margin-bottom: 0;
}

.price-block-list ul li {
	margin-bottom: 10px;
}

.price-block-link {
	position: absolute;
	bottom: 20px;
	left: 50%;
	transform: translateX(-50%);
	white-space: nowrap;
}

.price-block-link a {
	display: inline-block;
	color: #000;
	text-align: center;
	font-size: 16px;
	text-transform: uppercase;
	background-color: #7cc15a;
	padding: 10px 15px;
	text-decoration: none;
	transition: all .5s ease;
}

.price-block-link a:hover {
	background-color: #99cf7f;
}

@font-face {
	font-family: "Pangolin";
	src: url("../fonts/Pangolin/pangolin-regular.eot?#iefix")format("embedded-opentype"),
	url("../fonts/Pangolin/pangolin-regular.woff") format("woff"),
	url("../fonts/Pangolin/pangolin-regular.woff2") format("woff2"),
	url("../fonts/Pangolin/pangolin-regular.ttf") format("truetype");
