.advise-block {
	padding: 5px;
	margin-top: 20px;
	border-radius: 10px;
	background: url(../img/icon/quote-bg.png);
}

.advise-block::first-child {
	margin-top: 0;
}

.advise-block-fon {
	background-color: #fff;
	padding: 20px;
	border-radius: 10px;
	position: relative;
}

.advise-block-fon::after {
	position: absolute;
	background: transparent url(../img/icon/bolt.png) no-repeat 0 0/100% 100%;
	height: 30px;
	right: 18px;
	top: 18px;
	width: 30px;
}

.advise-block-fon::before {
	position: absolute;
	background: transparent url(../img/icon/bolt.png) no-repeat 0 0/100% 100%;
	height: 30px;
	top: 18px;
	left: 18px;
	width: 30px;
}

.advise-block-fon-block {
	margin: 30px 40px;
	position: relative;
}

.advise-block-fon-block::before {
	background: transparent url(../img/icon/bolt.png) no-repeat 0 0/100% 100%;
	height: 30px;
	position: absolute;
	bottom: -40px;
	left: -40px;
	width: 30px;
}

.advise-block-fon-block::after {
	position: absolute;
	background: transparent url(../img/icon/bolt.png) no-repeat 0 0/100% 100%;
	height: 30px;
	right: -40px;
	bottom: -40px;
	width: 30px;
}

.advise-block-img {
	float: left;
	margin-right: 30px;
	margin-bottom: 10px;
}

.advise-block-img img {
	width: 250px;
	height: 250px;
	border-radius: 10%
}

.advise-block-text {
	padding: 0;
}

.advise-block-text ul {
	list-style-type: none;
}

.advise-block-text ul li a {
	color: #7cc15a;
}

.video-container {
	margin-top: 20px;
	position: relative;
	padding-bottom: 56.25%;
	padding-top: 30px;
	height: 0;
	overflow: hidden;
}

.video-container iframe,
.video-container object,
.video-container embed {
	position: absolute;
	top: 0;
	border: 0;
	left: 0;
	width: 100%;
	height: 100%
}

.video-wrapper {
