.button-top {
	position: fixed;
	bottom: 20px;
	right: -120px;
	width: 40px;
	height: 40px;

	border-radius: 50%;
	background-color: #7cc15a;
	cursor: pointer;
	text-align: center;
	opacity: .6;
	z-index: 3;
	transition: all .5s ease;
}

.button-top .fa {
